import React, { useState } from 'react'
import { Formik, Form, Field, ErrorMessage } from 'formik'
import * as Yup from 'yup'
import PhoneInput from 'react-phone-number-input'
import { isValidPhoneNumber } from 'libphonenumber-js'
import 'react-phone-number-input/style.css'
import './contact-form.scss'
import emailjs from 'emailjs-com'

const USER_ID = 'qVenSVyTLlLVAmyAO'
const SERVICE_ID = 'service_6pxgkio'
const TEMPLATE_ID = 'template_eza8cnr'

emailjs.init(USER_ID)

const validationSchema = Yup.object().shape({
  fullName: Yup.string().required('     '),
  email: Yup.string()
    .email('  ')
    .required('   '),
  phone: Yup.string()
    .test('phone', '   ', (value) => {
      const phoneNumber = value.replace(/[^\d+]/g, '')
      const country = 'UA'
      return isValidPhoneNumber(phoneNumber, country)
    })
    .required('    ')
})

const ContactForm = () => {
  const initialValues = {
    fullName: '',
    email: '',
    phone: ''
  }

  const [isSubmitted, setIsSubmitted] = useState(false)

  const handleSubmit = (values, { resetForm }) => {
    const templateParams = {
      from_name: values.fullName,
      message: `Name: ${values.fullName}\nEmail: ${values.email}\nPhone: ${values.phone}`
    }

    emailjs
      .send(SERVICE_ID, TEMPLATE_ID, templateParams)
      .then((response) => {
        console.log('Email sent successfully!', response.status, response.text)
        setIsSubmitted(true)
        resetForm()

        setTimeout(() => {
          setIsSubmitted(false)
        }, 5000)
      })
      .catch((error) => {
        console.error('Error sending email:', error)
      })
  }

  return (
    <Formik
      initialValues={initialValues}
      validationSchema={validationSchema}
      onSubmit={handleSubmit}
    >
      {({ isSubmitting }) => (
        <Form className="form">
          <h2 className="form__title">
             <span></span>
            <br />  
          </h2>

          <div>
            <label htmlFor="fullName"></label>
            <Field
              className="form__input"
              type="text"
              id="fullName"
              name="fullName"
              placeholder="   "
            />
            <ErrorMessage
              name="fullName"
              component="div"
              className="form__error"
            />
          </div>

          <div>
            <label htmlFor="email"></label>
            <Field
              className="form__input"
              type="email"
              id="email"
              name="email"
              placeholder=" email"
            />
            <ErrorMessage
              name="email"
              component="div"
              className="form__error"
            />
          </div>

          <div>
            <label htmlFor="phone"></label>
            <Field name="phone">
              {({ field, form }) => (
                <PhoneInput
                  {...field}
                  id="phone"
                  name="phone"
                  placeholder="  "
                  onChange={(value) => form.setFieldValue('phone', value)}
                  defaultCountry="UA"
                />
              )}
            </Field>
            <ErrorMessage
              name="phone"
              component="div"
              className="form__error"
            />
          </div>

          <button className="form__btn" type="submit" disabled={isSubmitting}>
             
          </button>

          <span className="form__policy">
                
            <br />
            <a href="#">  </a>
          </span>

          {isSubmitted && (
            <div className="form__success">  !</div>
          )}
        </Form>
      )}
    </Formik>
  )
}

export default ContactForm
